	import tabbar from '../../common/tabbar.vue';
	export default {
		data() {
			return {
				weichat: 'wangquietforyou',
				sessionKey: '',
				openId: '',
				contentHeight: 0,
				isCanUse:uni.getStorageSync('wx_user_info') || true
			}
		},
		components:{
			tabBar:tabbar
		},
		onLoad() {
			//this.login();
			let winHeight      = uni.getSystemInfoSync().windowHeight;
			// 设计稿731 高度
			//let ratio = winHeight/731;
			//ratio = ratio.toFixed(2);
			this.contentHeight = winHeight-82+20;
			console.log(winHeight, this.contentHeight);
			console.log(uni.getStorageSync('wx_user_info'));
			console.log('onLoad....');
		},
		created() {
			console.log('created....');
		},
		computed: {
			userInfo: function() {
				return this.$store.getters.userInfo;
			}
		},
		methods: {
			// 填写个人信息
			personal_info() {
				uni.navigateTo({
					url:'/pages/user/complaint'
				});
			},
			handleComplaint() {
				this.getUserInfoFromWeixin();
				uni.navigateTo({
					url:'/pages/user/complaint'
				});
			},
			upgrade_vip() {
				uni.navigateTo({
					url:'/pages/user/upgrade_user_vip'
				});
			},
			copy_customer_wechat() {
				uni.setClipboardData({
					data: this.weichat,
					success: function(res) {
						uni.getClipboardData({
							success: function(res) {
								uni.showToast({
									title: '已复制到剪贴板'
								});
							}
						});
					}
				});
			},
			updateUserInfo() {
				console.log('更新用户信息!');
			},
			setUserInfo(userInfo) {
				this.$store.commit('setUserInfo', userInfo);
			},
			addWechat() {
				this.getUserInfoFromWeixin();
				// 这里弹框,弹出微信
				/*uni.showModal({
				    title: '提示',
				    content: '请添加导师微信',
					showCancel: false,
					mask: true,
				    success: function (res) {
				        if (res.confirm) {
				            console.log('用户点击确定');
				        } else if (res.cancel) {
				            console.log('用户点击取消');
				        }
				    }
				});*/
			},
			getUserInfoFromWeixin() {
				let _this = this;
				uni.getUserInfo({
					provider: 'weixin',
					success: function(infoRes) {
　　　　　　　　　　　　　　　　　　　	//获取用户信息后向调用信息更新方法
						console.log('infoRes', infoRes);
						_this.setUserInfo(infoRes.userInfo);
						_this.updateUserInfo();//调用更新信息方法
					}
				});
			},
			login() {
				let _this = this;
				uni.showLoading({
					title: '登录中...'
				});
			   // 1.wx获取登录用户code
				uni.login({
					provider: 'weixin',
					success: function(loginRes) {
						let code = loginRes.code;
						console.log('code'+code);
						uni.hideLoading();
						//非第一次授权获取用户信息
						this.getUserInfoFromWeixin()
						//2.将用户登录code传递到后台置换用户SessionKey、OpenId等信息
						/*uni.request({
							url: '服务器地址',
							data: {
								code: code,
							},
							method: 'GET',
							header: {
								'content-type': 'application/json'
							},
							success: (res) => {
								//openId、或SessionKdy存储//隐藏loading
								uni.hideLoading();
							}
						});*/
					}
				});
			},
			setUserInfoToStrorage(userInfo) {
				const userStr = JSON.stringify(userInfo);
				uni.setStorageSync({
					key: 'wx_user_info',
					data: userStr,
					success: function () {
						console.log('wxSetUserInfo...success');
					},
					fail:function() {
						console.log('wxSetUserInfo...fail');
					}
				});
			},
			getUserInfoFromStorage() {
				const userStr = uni.getStorageSync('wx_user_info');
				return JSON.parse(userStr);
			},
			// 手动授权方法,授权登录的时候,只调用一次
			wxGetUserInfo(e) {
				console.log('hehe');
				uni.getUserInfo({
					provider:'weixin',
					success: (res) => {
						console.log('res:', res);
						let userInfo = res.userInfo;
						console.log(userInfo);
						this.setUserInfo(userInfo);
						this.setUserInfoToStrorage(userInfo);
					},
					fail: () => {
						console.log("未授权");
					}
				});
			},
			// 手机登录时获取手机号码相关信息的函数
			getPhoneNumber(e) {
				console.log(e);
			}		
		}
	}
	
